* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.country-item__info {
  display: flex;
  align-items: center;
  flex-direction: row;
  gap: 20px;
}

.country-item__flag {
  width: 40px;
}
.country-item__name {
  font-size: 20px;
}
.country-item__name-header {
  font-size: 35px;
}
.country-item__population--bold,
.country-item__languages--bold,
.country-item__capital--bold {
  font-size: 20px;
}
.country-item__population--bold,
.country-item__languages--bold,
.country-item__capital--bold {
  font-weight: 700;
}
